body {
    margin: 0;
    padding: 0;
    background-color: #111; /* Match the background color */
    height: 100vh; /* Ensure it covers the full viewport height */
}

html {
    height: 100%;
}
.about-section, .journey {
    min-height: auto; /* Change from 100vh */
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Change from space-between */
    gap: 20px; /* Add controlled spacing between sections */
}