.typewriter h1 {
    overflow: hidden; /* Ensures the text doesn't wrap */
    border-right: 0.15em solid #ffeb3b; /* Creates a cursor effect */
    white-space: nowrap; /* Keeps the text on a single line */
    margin: 0 auto; /* Centers the text */
    font-size: 2.5rem;
    color: #ffeb3b;
    animation: typing 3s steps(30, end), blink-caret 0.5s step-end infinite;
  }
  
  @keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }
  
  @keyframes blink-caret {
    from, to {
      border-color: transparent;
    }
    50% {
      border-color: #ffeb3b;
    }
  }
  
  .home {
    height: 100vh; /* Full viewport height */
    display: flex;
    justify-content: center; /* Horizontally center */
    align-items: center; /* Vertically center */
    background-color: #000; /* Keep it professional with black background */
    color: #ffeb3b; /* Retain yellow color for emphasis */
    font-size: 3rem; /* Increase font size for better visibility */
    text-align: center;
  }
  
  .typewriter {
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
  }
  
  .particles {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1; /* Place behind content */
  }
  
  