.about-section {
    gap: 10px; /* Consistent gap between sections */
}

.about-section, .journey {
    min-height: auto; /* Change from 100vh */
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Change from space-between */
    gap: 20px; /* Add controlled spacing between sections */
}

.about-section h2 {
font-size: 2.5rem;
color: #ffc107;
margin-bottom: 30px;
text-align: left;
}

/* Introduction Section */
.about-intro {
display: flex;
flex-direction: column;
align-items: flex-start;
margin-bottom: 40px;
}

.intro-box {
background-color: #222;
padding: 20px;
border-radius: 10px;
box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
max-width: 600px;
}

.intro-box h3 {
font-size: 1.8rem;
color: #ffc107;
margin-bottom: 15px;
}

.skills {
display: flex;
gap: 10px;
flex-wrap: wrap;
}

.skill-tag {
background-color: #ffc107;
color: #111;
padding: 8px 15px;
border-radius: 20px;
font-size: 1rem;
font-weight: bold;
}

.journey {
    padding: 0;
    margin: 0;
    margin-top: -20px; /* Pull the entire journey section up */
}

.journey h3 {
    margin: 0;
    padding: 0;
    font-size: 2rem;
    color: #ffc107;
    margin-bottom: 10px; /* Add a small margin below the heading */
}

.timeline-container {
    padding: 0;
    margin: 0;
}


.timeline {
    position: relative;
    width: 100%;
    margin: 0;
    padding-left: 40px;
    margin-top: 15px; /* Add small controlled space below Journey heading */
}


@keyframes slide-in {
from {
    height: 0;
}
to {
    height: 100%;
}
}

/* Timeline block hover effect */
.timeline-block {
background: #222;
padding: 15px;
border-radius: 10px;
transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.timeline-block:hover {
transform: translateY(-5px);
box-shadow: 0px 5px 15px rgba(255, 193, 7, 0.3);
}

.timeline::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30px; /* Center the vertical line */
    width: 4px;
    background: #ffc107; /* Timeline color */
}

.timeline-item {
    position: relative;
    margin-bottom: 30px; /* Reduce space between items */
}


/* Adjust the first timeline item */
.timeline-item:first-child {
    margin-top: 0; /* Remove top margin from first item */
}

.timeline-item:last-child {
    margin-bottom: 0;
}

.timeline-item::before {
    content: '';
    position: absolute;
    left: -18px;
    top: 10;
    width: 15px;
    height: 15px;
    background: #ffc107; /* Circle color */
    border-radius: 50%;
    border: 3px solid #111; /* Match background color */
}

.timeline::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30px;
    width: 4px;
    background: #ffc107;
    margin-top: 0; /* Ensure line starts at the top */
}
/* Adjusting the Content Block */
.content {
background-color: #222;
padding: 20px 30px;
border-radius: 10px;
box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
flex: 1;
width: 100%; /* Ensure full width */
max-width: none; /* Remove max-width limit */
text-align: left;
margin-right: 0; /* Remove any margin on the right */
}

/* Title Styling (H4) */
.content h4 {
font-size: 1.5rem;
color: #ffc107;
margin-bottom: 10px;
text-align: left; /* Ensure left alignment */
}

/* Paragraph and Span Styling */
.content p, .content span {
font-size: 1rem;
color: #ddd;
margin-bottom: 8px;
text-align: left; /* Align to the left */
}

/* General button styling */
button {
background: linear-gradient(to right, #FFC107, #FF5722);
border: none;
color: white;
padding: 10px 20px;
border-radius: 20px;
font-size: 1rem;
font-weight: bold;
cursor: pointer;
transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Hover effect */
button:hover {
transform: scale(1.1);
box-shadow: 0px 0px 10px 3px rgba(255, 87, 34, 0.7);
}

/* Active state */
button:active {
transform: scale(1.05);
box-shadow: 0px 0px 15px 5px rgba(255, 193, 7, 0.8);
}

@media (max-width: 768px) {
    .about-section h2 {
        font-size: 2rem;
    }

    .intro-box h3, .content h4 {
        font-size: 1.5rem;
    }

    .timeline-item {
        margin-bottom: 30px; /* Reduce spacing on smaller screens */
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
